import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Users from "./Users";
import UserSignUp from "./UserSignUp";
import Game from "./Game";
import MyGames from "./MyGames";
import Navbar from "./Navbar";
import Invites from "./Invites";

const Path: any = "/uplara-client";

const Routes = () => {
  const RedirectToLogin = ({ component: Component, ...rest }: any) => {
    return (
      <Route
        {...rest}
        render={(props) =>
          localStorage.getItem("auth_token") ? (
            <Navbar>
              <Component {...props} />
            </Navbar>
          ) : (
            <Redirect to={`${Path}/sign-in`} />
          )
        }
      />
    );
  };

  const RedirectToHome = ({ component: Component, ...rest }: any) => {
    return (
      <Route
        {...rest}
        render={(props) =>
          localStorage.getItem("auth_token") ? (
            <Redirect to={Path} />
          ) : (
            <Component {...props} />
          )
        }
      />
    );
  };
  return (
    <Switch>
      <RedirectToLogin exact path={Path} component={Users} />
      <RedirectToHome exact path={`${Path}/sign-in"`} component={UserSignUp} />
      <RedirectToLogin path={`${Path}/my-games`} component={MyGames} />
      <RedirectToLogin path={`${Path}/invites`} component={Invites} />
      <RedirectToLogin path={`${Path}/game/:id`} component={Game} />
      <Redirect to={Path} />
    </Switch>
  );
};

export default Routes;
